.container {
  margin-left: auto;
  margin-right: auto;
  padding: 40px 20px 80px;
}

.sidebar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 40px 32px 100px;
  }
  .sidebar {
    flex-direction: row;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    display: inline-flex;
    max-width: 1440px;
    padding: 40px 24px 72px;
  }
  .sidebar {
    flex-direction: column;
  }
}
