.switch {
  width: 60px;
  height: 34px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 40px;
  width: 40px;
  background: #fff url("https://i.ibb.co/FxzBYR9/night.png") center no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 15px rgba(32, 32, 32, .24);
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  background: #fff url("https://i.ibb.co/7JfqXxB/sunny.png") center no-repeat;
  transform: translateX(24px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.theme-switch .background-icons {
  align-items: center;
  gap: 10px;
  display: flex;
}

.theme-switch .background-icon {
  cursor: pointer;
}

.theme-switch .background-icon:hover {
  transform: scale(1.1);
}

